/* eslint-disable multiline-ternary */
import { Fragment, useContext, useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import toast from "react-hot-toast"
import Cleave from "cleave.js/react"
import {
  Row,
  Col,
  Form,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  CardText
} from "reactstrap"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors, sortedObject } from "@utils"
import Select from "react-select"
import Flatpickr from "react-flatpickr"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import { useForm, Controller } from "react-hook-form"
import {
  storeName,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  getDataById
} from "../store"
import { getDataList as getDataListApp } from "../../app/store"
import { useDispatch, useSelector } from "react-redux"
import classnames from "classnames"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"

const ModalAddEdit = ({
  show,
  setShow,
  detailPage,
  formState,
  setFormState
}) => {
  const { t } = useTranslation()
  const { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const storeApp = useSelector((state) => state.apps_app)
  const storeUnit = useSelector((state) => state.units)
  const [selectedData, setSelectedData] = useState(store.selectedData)
  const { colors } = useContext(ThemeColors)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [idApp, setIdApp] = useState(null)
  const [unitOptions, setUnitOptions] = useState([])
  const [unitOptionsSelected, setUnitOptionsSelected] = useState({})
  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  const countChanges = (data) => {
    if (formState === "edit") {
      const storeData = { ...sortedObject(store.selectedData) }
      delete storeData["id"]
      for (let key in storeData) {
        if (!(key in data)) delete storeData[key]
      }
      // console.log(JSON.stringify(sortedObject(storeData)))
      // console.log(JSON.stringify(sortedObject(data)))
      return (
        JSON.stringify(sortedObject(storeData)) !==
        JSON.stringify(sortedObject(data))
      )
    } else {
      return true
    }
  }

  const updateDataToApi = (data) => {
    data["id_unit"] = unitOptionsSelected.value
    data["id_app"] = idApp.value
    if (countChanges(data)) {
      if (
        Object.values(data).every((field) => {
          return field !== null && field !== ""
        })
      ) {
        setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
        dispatch(putData(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setShow(!show)
            toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
            if (detailPage) {
              dispatch(getDataById(id))
            }
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const postDataToApi = (data) => {
    if (
      Object.values(data).every((field) => {
        return field !== null && field !== ""
      })
    ) {
      // console.log(unitOptionsSelected)
      data["id_unit"] = unitOptionsSelected
      data["id_app"] = idApp
      delete data["description"]
      // console.log(data)
      setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)
      dispatch(postData(data)).then((res) => {
        //
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setShow(!show)
          toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
          if (detailPage) {
            dispatch(getDataById(id))
          }
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key]?.length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  const handleReset = () => {
    dispatch(resetSelectedData())
  }

  const handleChangeUnit = (e) => {
    console.log(e)
    setUnitOptionsSelected(e.value)
    // setUnitOptionsSelected(Array.isArray(e) ? e.map((x) => x.value) : [])
  }

  useEffect(() => {
    if (storeApp.data.length === 0 || !storeApp.isLoading) {
      dispatch(getDataListApp({}))
    }

    setIdApp(
      storeApp.dataDropdown.filter(
        (el) => el.value === store.selectedData?.id_app
      )
    )
    const unitOptions = storeUnit.dataDropdown.filter((x) => x.value !== "")
    setUnitOptions(unitOptions)
    setUnitOptionsSelected(
      unitOptions.filter((el) => el.value === store.selectedData?.id_unit)
    )
  }, [])

  return (
    <Modal
      isOpen={show}
      toggle={() => {
        handleReset()
        setShow(!show)
      }}
      className="modal-dialog-centered modal-lg"
    >
      <UILoader
        blocking={store.isLoadingAddEdit || store.isLoadingDetail}
        loader={<Loader />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => {
            handleReset()
            setShow(!show)
          }}
        ></ModalHeader>
        <ModalBody className="px-sm-5 pt-50 pb-5">
          <div className="text-center mb-2">
            <h2 className="mb-1">
              {formState === "edit" ? t("Edit") : t("Add")} {t(pageTitle)}
            </h2>
          </div>
          <Form
            onSubmit={
              formState === "edit"
                ? handleSubmit(updateDataToApi)
                : handleSubmit(postDataToApi)
            }
          >
            <Row className="gy-1 pt-75">
              <Col md={12} xs={12}>
                <Label className="form-label" for="domain">
                  {t("Domain Address")}
                  <span className="text-danger">*</span>
                </Label>
                <Controller
                  defaultValue={store.selectedData?.domain ?? ""}
                  control={control}
                  id="domain"
                  name="domain"
                  render={({ field }) => (
                    <Input
                      {...field}
                      className={
                        errors.domain
                          ? "form-control is-invalid"
                          : "form-control"
                      }
                      id="domain"
                      invalid={errors.domain && true}
                    />
                  )}
                />
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="id_app">
                  {t("App")}
                </Label>
                <Select
                  id="id_app"
                  theme={selectThemeColors}
                  defaultValue={storeApp.dataDropdown.filter(
                    (el) => el.value === selectedData?.id_app
                  )}
                  onChange={(data) => {
                    setIdApp(data.value)
                  }}
                  options={storeApp.dataDropdown}
                  className={classnames("react-select", {
                    "is-invalid": idApp === null
                  })}
                  classNamePrefix="select"
                />
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="id_unit">
                  {t("Unit")}
                </Label>
                <Select
                  id="id_unit"
                  isClearable={false}
                  theme={selectThemeColors}
                  closeMenuOnSelect={true}
                  defaultValue={storeUnit.dataDropdown.filter(
                    (el) => el.value === selectedData?.id_unit
                  )}
                  onChange={(data) => {
                    handleChangeUnit(data)
                  }}
                  options={unitOptions}
                  className="react-select"
                  classNamePrefix="select"
                />
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="description">
                  {t("Description")}
                </Label>
                <Controller
                  defaultValue={store.selectedData?.description ?? ""}
                  control={control}
                  id="description"
                  name="description"
                  render={({ field }) => (
                    <Input
                      {...field}
                      className={
                        errors.domain
                          ? "form-control is-invalid"
                          : "form-control"
                      }
                      type="textarea"
                      id="description"
                      invalid={errors.description && true}
                    />
                  )}
                />
              </Col>

              <Col xs={12} className="text-center mt-2 pt-50">
                <Button type="submit" className="me-1" color="primary">
                  {t("Submit")}
                </Button>
                <Button
                  type="reset"
                  color="secondary"
                  outline
                  onClick={() => {
                    handleReset()
                    setFormState("add")
                    setShow(false)
                  }}
                >
                  {t("Cancel")}
                </Button>
              </Col>
            </Row>
          </Form>
        </ModalBody>
      </UILoader>
    </Modal>
  )
}

export default ModalAddEdit
